@mixin flex(){
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}


html,body{margin:0;padding:0;height:100%;color:#555}
body{-webkit-font-smoothing:antialiased;}

h1,h2,h3,h4,h5{font-family: 'Cabin', sans-serif;color:#3b4550;}
h1,h2{font-weight:700;}
h3,h4,h5{font-weight:400;}

section.container{
  	height:100%;
  	min-height:600px;

  	.col{
	  display:table;
	  float:left;
	  height:100%;

	  	.col-inner{
			display:table-cell;
			padding:20px 15px;
			vertical-align:middle;
		}
	}

	.col.col-25{
  		width:25%;
	}

	.col.col-33{
  		width:33%;
	}

	.col.col-50{
  		width:50%;
	}

	.col.col-100{
  		width:100%;
	}
}


/* Specific Sections */
#intro{
  position:relative;
  background: #5D35CE;
  background: -webkit-linear-gradient(135deg, #413168 15%, #4B369F 40%, #5D35CE 55%, #A16BD1 80%, #CE9EC5 95%);
  background: -o-linear-gradient(135deg, #413168 15%, #4B369F 40%, #5D35CE 55%, #A16BD1 80%, #CE9EC5 95%);
  background: -moz-linear-gradient(135deg, #413168 15%, #4B369F 40%, #5D35CE 55%, #A16BD1 80%, #CE9EC5 95%);
  background: linear-gradient(135deg, #413168 15%, #4B369F 40%, #5D35CE 55%, #A16BD1 80%, #CE9EC5 95%);
  color:#fff;

  h1{
  	font-size:4em;
  	margin:0px 0px 15px;
    color:#fff;
  }

  h3{
  	font-size:2em;
  	letter-spacing: 0.06em;
    font-weight: 300;
  	margin:0px 0px 15px;
    color:#fff;
  }

  .arrow-more{
    position:absolute;
    width:100%;
    height:60px;
    left:0;
    bottom:0;
    color:rgba(255,255,255,0.8);
    font-size:44px;
    text-align:center;
  }
}


#apps{

  h1{
    text-align:center;
  }

  h2{
    font-size:2.6em;
  }

  .iphone{
    @include flex();
    margin:0 auto;
    width: 267px;
    height: 553px;
    border-radius: 38px;
    background: #fff;
    box-shadow: inset 0 4px 7px 1px #fff,inset 0 -5px 20px rgba(173,186,204,.25),0 2px 6px rgba(0,21,64,.18),0 10px 20px rgba(0,21,64,.2);
  }

  .app-icon{
    width:240px;
    height:240px;
    border-radius:20%;
    background:#333;
    margin:0 auto 30px;
    overflow:hidden;
    box-shadow: 0px 1px 4px rgba(0,0,0,0.15);

    img{
      max-width:100%;
      max-height:100%;
    }
  }
}



/* Utility Classes */
.align-center{text-align:center;}


/* Animation Classes */


/* Responsive */
@media only screen and (max-width:767px){
  section.container:not(#intro){
    height:auto;
    min-height:600px;

    .col{
      display:block;
      float:none;

      .col-inner{
        display:block;
        padding:60px 15px;
      }
    }

    .col.col-25, .col.col-33, .col.col-50{
        width:100%;
    }
  }
}